/**
 * Formatea un valor numrico como moneda en formato abreviado
 * @param {number} value - Valor a formatear
 * @returns {string} Valor formateado
 */
export const formatCurrency = (value) => {
  if (value === undefined || value === null) return '$0';
  const numValue = typeof value === 'string' ? parseFloat(value) : value;
  if (isNaN(numValue)) return '$0';
  
  if (numValue >= 1000000) return `$${(numValue / 1000000).toFixed(1)}M`;
  if (numValue >= 1000) return `$${(numValue / 1000).toFixed(1)}K`;
  return `$${numValue.toFixed(0)}`;
};

/**
 * Limpia y convierte un valor de dlar en nmero
 * @param {string} value - Valor en formato de dlar (e.g., "$1,234.56")
 * @returns {number} - Valor numrico
 */
export const cleanDollarValue = (value) => {
  if (!value) return 0;
  if (typeof value === 'number') return value;
  
  // Remover el smbolo de dlar y las comas, luego convertir a nmero
  return parseFloat(value.replace(/[$,]/g, '')) || 0;
};

/**
 * Formatea un nmero como valor de dlar
 * @param {number} value - Valor numrico
 * @returns {string} - Valor formateado (e.g., "$1,234.56")
 */
export const formatDollarValue = (value) => {
  return new Intl.NumberFormat('en-US', {
    style: 'currency',
    currency: 'USD',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0
  }).format(value);
};

/**
 * Formatea un nmero como porcentaje
 * @param {number} value - Valor numrico
 * @returns {string} - Valor formateado (e.g., "12.34%")
 */
export const formatPercentage = (value) => {
  return new Intl.NumberFormat('en-US', {
    style: 'percent',
    minimumFractionDigits: 1,
    maximumFractionDigits: 1
  }).format(value / 100);
};